function displayPrimitves(point, nbPoints) {
    var points = [];
    for (let i = 0; i < nbPoints; i++)
        points.push(point)

    return <>{points} </>
}

function displayDates() {
    var point = (
        <div className="exp-circles">
            <i class="fas fa-circle"></i>
        </div>
    )

    var arrow = (
        <div className="exp-arrows">
            <i class="fas fa-long-arrow-alt-down fa-6x"></i>
        </div>
    )

    return (
        <>
            {point}
            {arrow}
            <div className="exp-dates">Septembre 2017</div>
            {displayPrimitves(point, 8)}
            <div className="exp-dates">Janvier 2018</div>
            {arrow}
            <div className="exp-dates">Fvrier 2018</div>
            {displayPrimitves(point, 2)}
            <div className="exp-dates">Juillet 2018</div>
            {arrow}
            <div className="exp-dates">Octobre 2018</div>
            {displayPrimitves(point, 31)}
            <div className="exp-dates">Janvier 2021</div>
            {arrow}
            {displayPrimitves(point, 30)}
            <div className="exp-dates">Maintenant</div>
        </>
    )
}

